import React from "react"
import { Card, CardHeader, CardBody, CardTitle, Row, Col, Button, Form, Input, Label, FormGroup, Table } from "reactstrap"
//   import userImg from "../../assets/img/portrait/small/avatar-s-18.jpg"
  import Checkbox from "../../components/@vuexy/checkbox/CheckboxesVuexy"
  import { Check, Lock } from "react-feather"

import "../../assets/scss/pages/users-profile.scss"

class Profile extends React.Component {
  state = {
    firstName: "",
    lastName: "",
    position: "",
    email: "",
    phone: "",
    password: ""
  }

  render() {
    return (
        <Card>
            <CardHeader>
                <CardTitle>User Profile</CardTitle>
            </CardHeader>
            <CardBody>
                <Row>
                    <Col sm="12">
                        <Form onSubmit={e => e.preventDefault()}>
                            <Row>
                                <Col md="6" sm="12">
                                    <FormGroup>
                                        <Label for="firstName">First Name<span style={{color: "red"}}>*</span></Label>
                                        <Input
                                            type="text"
                                            id="firstName"
                                        />
                                    </FormGroup>
                                </Col>
                                <Col md="6" sm="12">
                                    <FormGroup>
                                        <Label for="lastName">Last Name<span style={{color: "red"}}>*</span></Label>
                                        <Input
                                            type="text"
                                            id="lastName"
                                        />
                                    </FormGroup>
                                </Col>
                                <Col md="6" sm="12">
                                    <FormGroup>
                                        <Label for="email">E-Mail<span style={{color: "red"}}>*</span></Label>
                                        <Input
                                            type="text"
                                            id="email"
                                        />
                                    </FormGroup>
                                </Col>
                                <Col md="6" sm="12">
                                    <FormGroup>
                                        <Label for="positon">Position<span style={{color: "red"}}>*</span></Label>
                                        <Input
                                            type="text"
                                            id="positon"
                                        />
                                    </FormGroup>
                                </Col>
                                <Col md="6" sm="12">
                                    <FormGroup>
                                        <Label for="phone">Phone</Label>
                                        <Input
                                            type="text"
                                            id="phone"
                                        />
                                    </FormGroup>
                                </Col>
                                <Col md="6" sm="12">
                                    <FormGroup>
                                        <Label for="fax">Fax</Label>
                                        <Input
                                            type="text"
                                            id="fax"
                                        />
                                    </FormGroup>
                                </Col>
                                <Col md="6" sm="12">
                                    <FormGroup>
                                        <Label for="password">Password<span style={{color: "red"}}>*</span></Label>
                                        <Input
                                            type="text"
                                            id="password"
                                        />
                                    </FormGroup>
                                </Col>
                                <Col sm="12">
                                    <div className="permissions border px-2">
                                    <div className="title pt-2 pb-0">
                                        <Lock size={19} />
                                        <span className="text-bold-500 font-medium-2 ml-50">
                                        Security
                                        </span>
                                        <hr />
                                    </div>
                                    <Row>
                                        <Col sm="2"></Col>
                                        <Col sm="8">
                                            <Table borderless responsive>
                                                <thead>
                                                    <tr>
                                                        <th>Sales</th>
                                                        <th>Service</th>
                                                        <th>Manager</th>
                                                        <th>Admin</th>
                                                        <th>Support</th>
                                                    </tr>
                                                </thead>
                                                <tbody>
                                                    <tr>
                                                        <td>
                                                            <Checkbox
                                                                color="primary"
                                                                icon={<Check className="vx-icon" size={16} />}
                                                                label=""
                                                                defaultChecked={true}
                                                            />
                                                        </td>
                                                        <td>
                                                            <Checkbox
                                                                color="primary"
                                                                icon={<Check className="vx-icon" size={16} />}
                                                                label=""
                                                                defaultChecked={true}
                                                            />
                                                        </td>
                                                        <td>
                                                        <Checkbox
                                                            color="primary"
                                                            icon={<Check className="vx-icon" size={16} />}
                                                            label=""
                                                            defaultChecked={true}
                                                        />
                                                        </td>
                                                        <td>
                                                        <Checkbox
                                                            color="primary"
                                                            icon={<Check className="vx-icon" size={16} />}
                                                            label=""
                                                            defaultChecked={true}
                                                        />
                                                        </td>
                                                        <td>
                                                        <Checkbox
                                                            color="primary"
                                                            icon={<Check className="vx-icon" size={16} />}
                                                            label=""
                                                            defaultChecked={true}
                                                        />
                                                        </td>
                                                    </tr>
                                                </tbody>
                                            </Table>
                                    </Col>
                                        <Col sm="2"></Col>
                                    </Row></div>
                                </Col>
                                <Col
                                    className="d-flex flex-wrap mt-2 justify-content-end"
                                    sm="12"
                                >
                                    <Button.Ripple className="mr-1" color="primary">
                                        Save Changes
                                    </Button.Ripple>
                                    <Button.Ripple color="flat-warning">Reset</Button.Ripple>
                                </Col>
                            </Row>
                        </Form>
                    </Col>
                </Row>
            </CardBody>
      </Card>
    )
  }
}

export default Profile
